input[type=radio] + label::after {
  content: "";
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  border-radius: 50%;
}

input[type=radio] + label::before {
  left: 0;
  width: 2rem;
  height: 2rem;
  border: solid 2px #22b3c6;
  background-color: #ffffff;
}

input[type=radio] + label::after {
  left: 8.3%;
  width: 1.25rem;
  height: 1.25rem;
  background-color: #ea9a27;
  opacity: 0;
  visibility: hidden;
}

